*{margin: 0;
    padding: 0; 
    box-sizing: border-box;
    background-color: #e2eaec;
}

.cards{
    width: 100%;
    height: auto; 
} 

.card{
   margin-top: 3%;
   margin-bottom: 3%;
   margin-left: 14%;
   background-color: #fff;
    border-radius: 7px;
    width: 190px;
    text-align: center;
    transition:all 0.2s cubic-bezier(0.175, 0.885, 0, 1);
    box-shadow: 10px 10px 18px -8px black;
    float: left;
}

.card:hover{
    transform: scale(1.05,1.05); 
}

 .heading_style{
    padding: 30px 0; 
     text-align: center;
    background-color: rgba(32, 4, 4, 0.1);
    text-transform: uppercase;
    font-size: 23px;
    font-weight: 500;
    color:#fff;
    letter-spacing: 2px;
    
}


.card img{
    width: 190px;
    height: 150px;
    border-top-left-radius: 5%;
    border-top-right-radius: 5%;
}
.card_info{
    text-align: center;
    background-color: #fff;
    border-bottom-left-radius: 5%;
    border-bottom-right-radius: 5%;
    padding: 10px 0;
    }
.card_category{
    background-color: #fff;
    padding: 5px 0;
    text-transform: capitalize;
    color: #868686;
     

}

.card_title{
    background-color: #fff;  
    padding: 5px 0;
    text-transform: uppercase;
}

.card button:hover{
    transform: scale(1.05,1.05);
}

button{
cursor: pointer;
border-radius: 2px;
color: brown;
}
